.menu {
  display: flex;
  flex-direction: row;
  gap: 16px;

  &__link {
    color: #a8aebf;
    text-decoration: none;
    font-weight: 600;

    padding: 12px 16px;
    display: flex;
  }

  &__item {
    align-items: center;
  }

  &__rightArrow {
    margin-left: 8px;
  }
}